<template>
  <div>
    <!-- <v-data-table :headers="headers" :items="drinks" :search="search" sort-by="calories" class="elevation-1"> -->
    <template>
      <v-toolbar flat color="white">
        <v-text-field v-model="search" label="Search" single-line hide-details></v-text-field>
        <v-divider class="mx-4" inset vertical></v-divider>
        <v-spacer></v-spacer>
        <v-dialog v-model="dialog" max-width="500px">
          <template v-slot:activator="{ on }">
            <v-btn color="primary" dark class="mb-2" v-on="on">buat minuman</v-btn>
          </template>
          <v-card>
            <v-card-title>
              <span class="headline">{{ formTitle }}</span>
            </v-card-title>
            <v-card-text>
              <v-container>
                <v-row>
                  <v-col cols="12" sm="6" md="4">
                    <v-text-field v-model="editedItem.name" label="Drink name"></v-text-field>
                  </v-col>
                  <v-col cols="12" sm="6" md="4">
                    <v-combobox v-model="editedItem.coldorhot" :items="items" label="Cold/Hot"></v-combobox>
                  </v-col>
                  <v-col cols="12" sm="6" md="4">
                    <v-text-field v-model="editedItem.fat" label="Fat (g)"></v-text-field>
                  </v-col>
                  <v-col cols="12" sm="6" md="4">
                    <v-text-field v-model="editedItem.carbs" label="Carbs (g)"></v-text-field>
                  </v-col>
                  <v-col cols="12" sm="6" md="4">
                    <v-text-field v-model="editedItem.protein" label="Protein (g)"></v-text-field>
                  </v-col>
                </v-row>
              </v-container>
            </v-card-text>
            <v-card-actions>
              <v-spacer></v-spacer>
              <v-btn color="blue darken-1" text @click="close">Cancel</v-btn>
              <v-btn color="blue darken-1" text @click="save">Save</v-btn>
            </v-card-actions>
          </v-card>
        </v-dialog>
      </v-toolbar>
    </template>
    <v-layout class="mx-4">
      <v-flex>
        <template>
          <v-row>
            <v-col cols="3" v-for="minuman in drinks" :key="minuman.id">
              <v-card class="mx-auto" max-width="344" hover>
                <v-card-text>
                  <div>MINUMAN</div>
                  <p class="display-1 text--primary">
                    {{minuman.name}}
                  </p>
                  <p>Hot/Cold : {{minuman.coldorhot}}</p>
                  <p>Fat : {{minuman.fat}}</p>
                  <p>Carbs : {{minuman.carbs}}</p>
                  <p>Protein : {{minuman.protein}}</p>
                </v-card-text>
                <v-card-actions>
                  <v-btn text color="deep-purple accent-4" @click="edititem(minuman)">
                    EDIT
                  </v-btn>
                  <v-btn text color="deep-purple accent-4" @click="deleteitem(minuman)">
                    Delete
                  </v-btn>
                  <!-- <v-dialog v-model="dialogDel" max-width="290">
                    <v-card>
                      <v-card-title class="headline">Delete this drink?</v-card-title>

                      <v-card-text>
                        Deleting this data will affect permanently.
                      </v-card-text>

                      <v-card-actions>
                        <v-spacer></v-spacer>

                        <v-btn color="grey darken-1" text @click="dialogDel = false">
                          Cancel
                        </v-btn>

                        <v-btn color="red darken-1" text @click="deleteitem(minuman)">
                          Delete
                        </v-btn>
                      </v-card-actions>
                    </v-card>
                  </v-dialog> -->
                  </v-card-actions>
              </v-card>
            </v-col>
          </v-row>
        </template>
      </v-flex>
    </v-layout>
    <!-- <template> -->
      <!-- <v-icon class="mr-1" @click="edititem(item)">mdi-silverware</v-icon>
      <v-icon class="mr-1" @click="dialogDel = true">mdi-delete</v-icon> -->
    
    <!-- </template> -->
  <!-- </v-data-table> -->
  </div>
</template>

<script>

import { drinksRef } from '../firebase';

  export default {
    data: () => ({
      cek: -1,
      search: '',
      dialog: false,
      dialogDel: false,
      items: [
          "Cold",
          "Hot"
      ],
      headers: [{
          text: 'Drinks',
          align: 'left',
          sortable: false,
          value: 'name',
        },
        {
          text: 'Cold/Hot',
          value: 'coldorhot'
        },
        {
          text: 'Fat (g)',
          value: 'fat'
        },
        {
          text: 'Carbs (g)',
          value: 'carbs'
        },
        {
          text: 'Protein (g)',
          value: 'protein'
        },
        {
          text: 'Actions',
          value: 'action',
          sortable: false
        },
      ],
      drinks: [],
      editedIndex: -1,
      editedItem: {
        name: '',
        coldorhot: '',
        fat: 0,
        carbs: 0,
        protein: 0,
      },
      defaultItem: {
        name: '',
        coldorhot: '',
        fat: 0,
        carbs: 0,
        protein: 0,
      },
    }),
    firebase: {
      drinks : drinksRef
    },
    computed: {
      formTitle() {
        return this.cek === -1 ? 'New Item' : 'Edit Item'
      },
    },
    watch: {
      dialog(val) {
        val || this.close()
      },
    },
    methods: {
      close() {
        this.dialog = false
      },
      save() {
        // methods untuk save
        if (this.cek > -1) {
          drinksRef.child(this.editedIndex).set({name: this.editedItem.name,coldorhot: this.editedItem.coldorhot,
          fat: this.editedItem.fat, carbs: this.editedItem.carbs, protein: this.editedItem.protein})
          this.clear()
        } else {
          drinksRef.push({name: this.editedItem.name, coldorhot: this.editedItem.coldorhot,
          fat: this.editedItem.fat, carbs: this.editedItem.carbs, protein: this.editedItem.protein})
          this.clear()
        }
        this.close()
      },
      edititem(item) {
        this.editedIndex = item['.key']
        this.cek = 0
        this.editedItem = Object.assign({}, item)
        this.dialog = true
      },
      deleteitem(item) {
        confirm('Are you sure you want to delete this item?') && 
        drinksRef.child((item['.key'])).remove()
      },
      clear(){
        this.editedItem={}
        this.editedItem.name=''
        this.editedItem.coldorhot=''
        this.editedItem.fat=0
        this.editedItem.carbs=0
        this.editedItem.protein=0
        this.cek=-1
      }
    },
  } 
</script>
